//HEADER-------------------------------------------------------------------

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $corHeaderMobile;
  padding: 8px 16px;
  @include sombra;
  img {
    width: 40px;
    @include d(g) {
      display: none;
    }
  }
  @include d(g) {
    background-color: $corHeaderMobile;
    display: flex;
    gap: 32px;
    justify-content: flex-end;
    padding: 16px 60px;
    height: 80px;
    box-sizing: border-box;
  }
}

.header-menu i::before {
  content: $menu;
  @include d(g) {
    display: none;
  }
}

.header-pesquisa i::before {
  content: $notificacao;
}

.header-perfil {
  display: none;
  @include d(g) {
    display: grid;
    grid-template-columns: repeat(3, auto);
    column-gap: 8px;
    align-items: center;
    justify-content: flex-end;
    color: $fonteAlternativa;
    padding: 8px 32px;
    border-right: 1px solid $fonteAlternativa;
    &::before {
      content: "";
      display: block;
      width: 32px;
      height: 32px;
      background-image: url("assets/img/profile_2.png");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }
    &::after {
      content: $seta-baixo;
      color: $fontePrincipal;
      @include font(medio);
    }
  }
}

// MENU LATERAL ----------------------------------------------------------
.menu-lateral {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: $corMenuLateral;
  width: 75vw;
  height: 100vh;
  position: absolute;
  left: -100vw;
  transition: 0.5s;
  img {
    width: 118px;
    align-self: center;
  }
  a {
    padding-left: 64px;
    height: 64px;
    color: $fonteAlternativa;
    display: flex;
    align-items: center;
    &::before {
      content: "";
      width: 24px;
      height: 24px;
      font-size: 24px;
      position: absolute;
      left: 24px;
    }
    &.menu-lateral-inicio::before {
      content: $inicio;
    }
    &.menu-lateral-videos::before {
      content: $videos;
    }
    &.menu-lateral-picos::before {
      content: $picos;
    }
    &.menu-lateral-integrantes::before {
      content: $integrantes;
    }
    &.menu-lateral-camisas::before {
      content: $camisas;
    }
    &.menu-lateral-pinturas::before {
      content: $pinturas;
    }
    &.ativo {
      color: $fontePrincipal;
      padding-left: 56px;
      border-left: 8px solid $fontePrincipal;
    }
  }
  &.ativo {
    left: 0px;
    transition: 0.5s;
  }
  @include d(g) {
    position: static;
    width: 200px;
    grid-column: 1;
    grid-row: 1 / span 2;
  }
}

// CONTAINER PRINCIPAL --------------------------------------------------

.principal {
  padding: 24px 16px;
  @include d(g) {
    display: grid;
    gap: 16px;
    grid-template-columns: auto 1fr auto;
    grid-template-areas:
      "titulo-pagina titulo-pagina titulo-pagina"
      "bem-vindo bem-vindo videos-recentes"
      "secoes-vistas secoes-vistas secoes-vistas"
      "albuns arte-destaque arte-destaque"
      "clipes-alta clipes-alta clipes-alta";
    column-gap: 32px;
    padding: 16px 60px;
    grid-column: 2;

    .bem-vindo {
      grid-area: bem-vindo;
    }
    .videos-recentes {
      grid-area: videos-recentes;
    }
    .secoes-vistas {
      grid-area: secoes-vistas;
    }
    .albuns {
      grid-area: albuns;
    }
    .arte-destaque {
      grid-area: arte-destaque;
    }
    .clipes-alta {
      grid-area: clipes-alta;
    }
  }
}

.titulo-pagina {
  grid-area: titulo-pagina;
  @include font(pequeno2);
  font-weight: bold;
}

// CARTOES --------------------------------------------------------------

.cartao {
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
  background-color: $corCartao;
}

.cartao-principal {
  margin-bottom: 16px;

  .cartao-imagem-mobile {
    @include d(g) {
      display: none;
    }
  }
  .cartao-imagem-desktop {
    display: none;
    @include d(g) {
      display: block;
    }
  }

  @include d(g) {
    margin-bottom: 0;
  }
}

.cartao-conteudo {
  padding: 16px;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 8px;

  p {
    grid-column: span 2;
  }

  span {
    display: flex;
    align-items: center;
    color: $fonteAlternativa;
    &.cartao-info-tempo::before {
      content: $relogio;
      margin-right: 8px;
    }
    &.cartao-info-visualizacao::before {
      content: $visualizacao;
      margin-right: 8px;
    }
  }
}
.cartao-titulo {
  @include font(pequeno2);
  font-weight: bold;
}
.cartao-perfil {
  color: $fonteAlternativa;
  @include font(pequeno1);
}

.cartao-botao {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $corBotao;
  width: 40px;
  height: 40px;
  border-radius: 15px;
  @include font(pequeno1);
  justify-self: flex-end;

  &.cartao-botao-play::before {
    content: $play;
    @include font(medio);
  }
  &.cartao-botao-destaque {
    width: 100%;
    grid-column: span 2;
    &::before {
      margin-right: 8px;
    }
    @include d(g) {
      grid-column: auto;
      padding: 16px 8px;
    }
  }
}

.cronologico {
  display: none;

  .cronologico-link {
    @include font(pequeno1);
    font-weight: 600;
    color: $corLinks;
    justify-self: flex-end;
  }

  ul {
    display: grid;
    row-gap: 16px;
    grid-column: span 2;
    li {
      display: grid;
      grid-template-columns: auto 1fr;
      gap: 8px;
      img {
        width: 32px;
        grid-row: span 2;
      }
      h4 {
        @include font(pequeno1);
        font-weight: 700;
        line-height: 1.2;
      }
      p {
        @include font(pequeno1);
        color: $fonteAlternativa;
      }
    }
  }

  @include d(g) {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    row-gap: 24px;
    width: 256px;
    padding: 16px;
    box-sizing: border-box;
    align-self: flex-start;
  }
}

//SEÇÕES ------------------------------------------------------------------
.section {
  display: grid;
  gap: 16px;
  @include d(g) {
    grid-template-columns: repeat(4, 1fr);
  }
}

.titulo-section {
  @include font(pequeno2);
  font-weight: 700;
  @include d(g) {
    grid-column: 1/ -1;
  }
}

