// MIXINS
@mixin sombra() {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.16);
}
$font: "Open Sans", "icones", sans-serif;

@mixin font($tamanho) {
  font-family: $font;
  @if $tamanho == pequeno1 {
    font-size: 0.9rem;
  } @else if $tamanho == pequeno2 {
    font-size: 1.2rem;
  } @else if $tamanho == medio {
    font-size: 1.5rem;
  } @else if $tamanho == grande {
    font-size: 2rem;
  }
}
